* {
    font-family: 'Arvo', serif;
    box-sizing: border-box;
}

:root {
    --main-color: 230;
    --first-color: hsl(var(--main-color), 96%, 62%);
    --first-color-alt: hsl(var(--main-color), 57%, 53%);
    --first-color-lighter: hsl(var(--main-color), 92%, 85%);
}

body {
    background: #fff;
}

.container {
    padding: 2em;
}

.header {
    display: flex;
    justify-content: space-between;
    align-items: baseline;
}

.status {
    font-size: 1em;
    color: #333;
}

.status__online {
    color: #1E9600;
    font-weight: bold;
}

.status__offline {
    color: #FF0000;
    font-weight: bold;
}

.band-container {
    display: flex;
}

.band-chart {
    flex: 0 0 auto;
    padding-right: 30px;
    width: 60%;
}

#myChart {
    box-shadow: 0px 0px 20px #ccc;
    border-radius: 10px;
}

.band-chart__title {
    font-size: 1em;
    margin-bottom: 15px;
}

.band-options {
    flex: 0 0 auto;
    width: 40%;
}

.band-add {
    margin-bottom: 30px;
}

.band-add__title {
    font-size: 1em;
    margin-bottom: 15px;
}

.form__input {
    border-radius: 10px;
    border: 2px solid #aaa;
    display: block;
    font-size: .8em;
    margin-bottom: 10px;
    outline: none;
    padding: 10px;
    width: 100%;
}

.form__input:hover {
    border: 2px solid hsl(230, 96%, 62%);
    border: 2px solid var(--first-color);
}

.form__input:focus {
    border: 2px solid hsl(230, 96%, 62%);
    border: 2px solid var(--first-color);
}

.form__submit {
    background-color: hsl(230, 96%, 62%);
    background-color: var(--first-color);
    border-radius: 10px;
    border: none;
    color: #fff;
    display: block;
    padding: 10px;
    width: 100%;
}

.form__submit:hover {
    background-color: hsl(230, 57%, 53%);
    background-color: var(--first-color-alt);
}

.band-list__title {
    font-size: 1em;
    margin-bottom: 15px;
}

.content-table {
    border-collapse: collapse;
    font-size: .8em;
    border-radius: 10px 10px 0 0;
    width: 100%;
    overflow: hidden;
    box-shadow: 0px 0px 20px #ccc;
}

.content-table thead tr {
    background-color: hsl(230, 92%, 85%);
    background-color: var(--first-color-lighter);
    text-align: left;
    font-weight: bold;
}

.content-table th {
    padding: 15px 10px;
}

.content-table td {
    padding: 0 10px;
}

.content-table tbody tr {
    border-bottom: 1px solid #ddd;
}

.content-table tbody tr:nth-of-type(even) {
    background-color: #eee;
}

.content-table tbody tr:last-of-type {
    border-bottom: 3px solid hsl(230, 96%, 62%);
    border-bottom: 3px solid var(--first-color);
}

.content-table__btn-remove {
    background: hsl(0, 96%, 62%);
    border-radius: 5px;
    border: none;
    color: #fff;
    font-size: .8em;
    padding: 5px;
}

.content-table__btn-remove:hover {
    background: hsl(0, 96%, 58%);
}

.content-table__btn-add {
    background: hsl(230, 96%, 62%);
    background: var(--first-color);
    border-radius: 5px;
    border: none;
    color: #fff;
    font-size: .8em;
    padding: 5px;
}

.content-table__btn-add:hover {
    background: hsl(230, 57%, 53%);
    background: var(--first-color-alt);
}

.content-table__input {
    border-radius: 5px;
    border: 1px solid #aaa;
    display: block;
    font-size: .9em;
    outline: none;
    padding: 5px;
    width: 100%;
}

.conten-table__input:hover {
    border: 2px solid hsl(230, 96%, 62%);
    border: 2px solid var(--first-color);
}

.spinner {
    display: flex;
    height: 60vh;
    justify-content: center;
    align-items: center;
}

@media screen and (max-width: 800px) {
    .container {
        padding: 1em;
    }
    .header {
        display: flex;
        flex-wrap: wrap;
    }
    .header__title {
        margin-bottom: 0;
        font-size: 1.5em;
    }
    .band-container {
        display: block;
    }
    .band-chart {
        margin-top: 30px;
        padding-right: 0;
        width: 100%;
    }
    .band-options {
        margin-top: 30px;
        width: 100%;
    }
}
